<template lang="pug">
  header
    .container(:class="{'has-layer': toggled}")
      router-link.logo(:to="{path: '/'}")
      .btn-menu(v-on:click="toggleMenu()")
        font-awesome-icon(icon="bars")
      .nav(:class="{'show': toggled}")
        .btn-close(@click="toggleMenu()")
          font-awesome-icon(icon="times-circle")
        ul.rows
          li.nav-item(v-for="(item, key) in navs")
            router-link(:to="{path: key}", :class="{'is-highlight': key === 'onlinedonate'}") {{ item }}
</template>

<script>
export default {
  name: 'HeaderNav',
  data () {
    return {
      toggled: false,
      navs: {
        'recruit': '志工招募',
        'donate': '捐贈訊息',
        'class': '偏鄉教室',
        'contact': '聯絡我們',
        'onlinedonate': '❤ 線上捐款'
      },
      recruit: {
        'recruit-apply': '志工申請/種類',
        'recruit-detail': '工作內容/地點',
        'recruit-must': '志工須知',
        'recruit-life': '食衣住行',
        'recruit-before': '理性認知'
      },
      class: {
        'class-1': 'Type1',
        'class-2': 'Type2'
      }
    }
  },
  methods: {
    toggleMenu () {
      this.toggled = !this.toggled
    }
  }
}
</script>

<style lang="sass" scoped>
.container
  +fs($ta: right)
  &.has-layer
    &::before
      +pos($p: fixed, $t: 0, $r: 0, $b: 0)
      +size($w: 100vw, $h: 100vh)
      content: ''
      display: block
      background-color: rgba(#000, .5)
      z-index: 3

.logo
  +size($w: 100px, $h: 100px)
  +pos($p: absolute, $t: 0, $l: 0)
  background-image: url('/static/images/img-logo.png')
  background-size: contain
  display: inline-block
  background-repeat: no-repeat
  +rwd(tbl)
    +size($w: 60px, $h: 60px)

.nav
  display: inline-block
  width: calc(100% - 120px)
  max-width: 720px
  margin-top: 22px
  &-item
    +fs($ta: right)
    width: 0
    flex: 1 1 auto
    a
      +fs($fz: 20px, $c: $c-smoke, $fw: bolder, $lh: 20px)
      padding: 16px 16px 10px
      &.is-highlight
        +fs($c: $c-primary)
        border: 1px solid $c-primary
        border-radius: 2px
      &:hover
        +fs($c: $c-primary)
  +rwd(tbl)
    +pos($p: fixed, $t: 0, $r: 0, $b: 0)
    +size($w: 220px, $h: 100vh)
    background-color: #fff
    margin-top: 0
    padding-top: 0
    display: none
    z-index: 4
    box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.2)
    &.show
      display: block
    .rows
      display: block
    &-item
      +fs($ta: center)
      +size($w: 100%)
      a
        padding: 20px

.btn-close
  +fs($fz: 32px, $c: #fff, $ta: center)
  display: none
  padding: 10px
  cursor: pointer
  background-color: $c-grey
  +rwd(tbl)
    display: block

.btn-menu
  +fs($fz: 24px, $c: $c-smoke)
  +pos($p: absolute, $t: 20px, $r: 20px)
  +size($w: 40px, $h: 40px)
  display: none
  z-index: 1
  cursor: pointer
  +rwd(tbl)
    display: block
</style>



