<template lang="pug">
  .container.rows.wrapRWD
    .card(v-for="(type, index) in typeMapping")
      h3 {{ type }}
      ul.rows
        li.item(v-for="report in getLastReports(index)")
          a(:href="report.url")
            h4 {{ report.year }}<br/>
            h3 Q{{ report.quater }}
</template>

<script>
export default {
  name: 'HomeCards',
  data () {
    return {
      typeMapping: {
        finance: '最新財報',
        regular: '最新季報'
      },
      reports: [
        {
          type: 'finance',
          year: 2018,
          quater: 1,
          url: '#'
        },
        {
          type: 'regular',
          year: 2018,
          quater: 1,
          url: '#'
        },
        {
          type: 'finance',
          year: 2017,
          quater: 4,
          url: '#'
        },
        {
          type: 'regular',
          year: 2017,
          quater: 4,
          url: '#'
        },
        {
          type: 'finance',
          year: 2017,
          quater: 3,
          url: '#'
        },
        {
          type: 'regular',
          year: 2017,
          quater: 3,
          url: '#'
        }
      ]
    }
  },
  methods: {
    getLastReports (type) {
      const sametype = this.reports.filter(s => s.type === type)
      return sametype.slice(0, 3)
    }
  }
}
</script>

<style lang="sass" scopped>

.card
  +fs($c: $c-black, $ta: center)
  padding: 20px
  border-radius: 2px
  background-color: #ecf0f1
  width: 0
  flex: 1 1 auto
  margin: 20px 10px
  h3
    padding: 10px 0

.item
  +fs($fz: 22px, $fw: 500)
  width: 0
  flex: 1 1 auto
  margin: 10px 10px
  border-radius: 2px
  padding: 12px
  background-color: rgba(255, 255, 255, .6)
</style>



