<template lang="pug">
  section
    .overSlide
      h1 {{ content.title }}
      h3(v-show="content.subTitle") {{ content.subTitle }}
      a.btn-cta(v-show="content.btnUrl", :href="content.btnUrl") 線上捐款
    .hero(:class="{['no-' + content.imageNo]: content.imageNo }")
</template>

<script>
export default {
  name: 'Hero',
  props: {
    content: Object
  },
  data () {
    return {
    }
  }
}
</script>

<style lang="sass">
section
  +size($h: $heroH)
  position: relative
  &::before
    +size($h: $heroH, $w: 100%)
    +pos($p: absolute, $t: 0, $l: 0)
    content: ''
    display: inline-block
    background: linear-gradient(to top, rgba(0,0,0,0.5) 0%,rgba(0,0,0,0) 100%)
    z-index: 1

.overSlide
  +fs($c: #fff)
  +pos($p: absolute, $b: 50px, $l: 50px)
  z-index: 2
  h3
    +fs($c: $c-light-grey)
    padding: 20px 20px 0
  h1
    +fs($fw: bold, $lh: 48px)
    padding: 20px 20px 0
  a.btn-cta
    +fs($fz: 22px, $fw: bold, $lh: 22px)
    background-color: $c-primary
    border-radius: 2px
    padding: 16px 20px 8px
    margin: 20px
    border: 1px solid $c-primary
    &:hover
      +fs($c: $c-primary)
      background-color: #fff

.hero
  +size($h: $heroH, $w: 100%)
  +fs($ta: center)
  +pos($p: absolute, $b: t, $l: 0)
  background-size: cover
  background-position: 50% 50%
  z-index: 0
  background-attachment: fixed
  background-repeat: no-repeat
  @for $i from 1 through 6
    &.no-#{$i}
      background-image: url('/static/images/img-hero-0#{$i}.jpg')

</style>



